.TodoItem {
    margin-left: auto;
    margin-right: auto;

    border-radius: 5px;
    box-shadow: 1px 2px 5px 1px #2AC1BC;
    padding: 1rem;
    display: flex;
    align-items: center;
}

.TodoItem + .TodoItem {
    margin-top: 15px;
}

.content {
    cursor: pointer;
    flex: 1;
    display: flex;
    align-items: center;
}

.content svg {
    font-size: 1.5rem;
    color: #2AC1BC;
}
.content .text {
    margin-left: 0.5rem;
    flex: 1;
}

.content.checked .text {
    color: gray;
    text-decoration: line-through;
    cursor: pointer;
    opacity: 0.6;
}